import React from 'react';
import Container from '@material-ui/core/Container';
// import IconButton from '@material-ui/core/IconButton';

import './App.css';
import ZH from './ZH';
import EN from './EN';

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      // lang: (navigator.language || navigator.userLanguage).indexOf('zh') !== -1 ? 'zh' : 'en',
      lang: 'en', // Use english by default
    }
  }

  render() {
    return (
      <div>
        {/* <div className='lang-button-background'></div> */}
        {/* <IconButton className='lang-button' onClick={()=>{this.handleToggleLang()}} color='primary'>
          {this.state.lang === 'en' ? '中' : 'EN'}
        </IconButton> */}
        <Container maxWidth="md">
          {this.state.lang === 'en' ? <EN /> : <ZH />}
        </Container>
      </div>
    );
  }

  handleToggleLang() {
    if (this.state.lang === 'en') {
      this.setState({
        lang: 'zh'
      });
    } else {
      this.setState({
        lang: 'en'
      });
    }
  }
}

export default App;
